import React from 'react';
import { Divider } from 'antd';
import styled from 'styled-components';
import {
  GROOM_NAME,
  GROOM_FATHER_NAME,
  GROOM_MOTHER_NAME,
  BRIDE_NAME,
  BRIDE_FATHER_NAME,
  BRIDE_MOTHER_NAME,
} from '../Config';
import GroovePaper from '../Assets/GroovePaper.png';

const Wrapper = styled.div`
  background: #efebe9;
  background-image: url(${GroovePaper});
  padding-top: 42px;
  padding-left: 42px;
  padding-right: 42px;
  width: 100%;
`;

const Title = styled.p`
  font-size: 2vh;
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
  text-align: center;
`;

const Content = styled.p`
  font-size: 1.75vh;
  line-height: 1.75;
  opacity: 0.75;
  margin-bottom: 16px;
  width: 100%;
  text-align: center;
`;

const GroomBride = styled.p`
  font-size: 1.75vh;
  line-height: 1.75;
  opacity: 0.85;
  margin-bottom: 0px;
  width: 100%;
  text-align: center;
`;

const Gretting = () => {
  return (
    <Wrapper>
      <Divider style={{ marginTop: 0, marginBottom: 32 }} plain>
        <Title>초대합니다</Title>
      </Divider>
      <Content>
        새로운 마음과 새 의미를 간직하며 <br/>
        저희 두 사람이 새 출발의 첫걸음을 내딛습니다.<br/><br/>
        좋은 꿈, 바른 뜻으로 올바르게 살 수 있도록 <br/>
        축복과 격려 주시면<br/>
        더없는 기쁨으로 간직하겠습니다<br/>
        <br></br>
        <br></br>
      </Content>
      <GroomBride>
        
        {/* <b>{GROOM_FATHER_NAME}</b> · <b>{GROOM_MOTHER_NAME}</b>의 <span style={{padding: "5px"}}></span>아들<span style={{padding: "5px"}}></span><b>{GROOM_NAME}</b>
        <br />
        <b>{BRIDE_FATHER_NAME}</b> · <b>{BRIDE_MOTHER_NAME}</b>의 <span style={{padding: "5px"}}></span>&nbsp;&nbsp;딸&nbsp;&nbsp;<span style={{padding: "5px"}}></span><b>{BRIDE_NAME}</b>
        <br></br>
        <br></br> */}
      </GroomBride>
    </Wrapper>
  );
};

export default Gretting;
